<template>
  <div class="home center store-bg padding">
    <img class="logo" src="../../assets/logo-white.svg" />
    <h2>Bitte melde dich an</h2>
    <button class="outline" id="google-login" @click="login('google')">
      Mit Google anmelden
    </button>

    <button class="outline" id="facebook-login" @click="login('facebook')">
      Mit Facebook anmelden
    </button>

    <button @click="switchToView('forth', '/email')" tag="button" class="outline">
      Mit Email Adresse anmelden
    </button>
    
    <!-- <router-link to='/admin' tag="button" class="outline">Admin</router-link> -->
    

  </div>
</template>

<script>
import switchToView from '@/mixins/switchToView';
export default {
  name: 'home',
  data() {
    return {
      uid: null
    }
  },
  computed: {
    userName() {
      return this.$store.getters.userName;
    }
  },
  methods: { 
    login(loginService) {
      let provider;
      this.$store.dispatch('updateIsLoading', true);

      if (loginService === 'google' || loginService === 'facebook') {
        if (loginService === 'google') {
          provider = new this.$firebase.auth.GoogleAuthProvider();
        }

        if (loginService === 'facebook') {
          provider = new this.$firebase.auth.FacebookAuthProvider();
        }
        
        this.$firebase.auth().signInWithRedirect(provider);
      }
      
    }
  },

  mounted() {
    // this.$firebase.auth().getRedirectResult().then((result) => { });
  },
  mixins: [switchToView]
}
</script>

