<template>
  <button class="toggle-sidepanel icon" :class="active ? 'active' : false" @click="toggle">
    <span>Show sidepanel</span>
  </button>
</template>
<script>
  export default {
    data() {
      return {
        active: false
      }
    },

    methods: {
      toggle() {
        this.$emit('toggleSidepanel');
        this.active = !this.active;
      }
    },
    props: ['showSidepanel'],
    watch: {
      showSidepanel() {
        this.active = this.showSidepanel;
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import '@/styles/variables.scss';
  .toggle-sidepanel {
    display: block;
    width: 50px;
    height: 50px;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 21px;
    color: $color2;
    z-index: 60;
    
    &:before {
      content: '\f007';
    }
    &.active:before {
      content: '\f00d'
    }
  }
</style>