<template>
  <header v-if="loggedIn" :class="invert ? 'invert' : false">
    <div class="header__inner">
      <backButton />

      <toggleSidepanel :showSidepanel="showSidepanel" @toggleSidepanel="showSidepanel = !showSidepanel"/>
      
      <transition name="slide">
        <sidepanel v-if="showSidepanel">
          <h3 v-if="userName">{{ userName }}</h3>
          <logout></logout>
          <button v-if="$route.fullPath !== '/welcome'" @click="switchToView('back', '/welcome')">Home</button>
          <router-link v-if="$route.fullPath !== '/events'" to="/events" tag="button">Meine Buchungen</router-link>
          <router-link v-if="$route.fullPath !== '/services'" to="/services" tag="button">Neue Buchung</router-link>
          <router-link v-if="$route.fullPath !== '/about'" to="/about" tag="button">ber uns</router-link>
        </sidepanel>
      </transition>
      
      <blackout :showBlackout="showSidepanel" @clickedBlackout="showSidepanel = !showSidepanel"/>
    </div>
  </header>
</template>
<script>
import logout from '@/components/Logout'
import backButton from '@/components/BackButton'
import toggleSidepanel from '@/components/ToggleSidepanel'
import blackout from '@/components/Blackout'
import sidepanel from '@/components/Sidepanel'
import switchToView from '@/mixins/switchToView'

export default {
  data() {
    return {
      showSidepanel: false
    }
  },
  computed: {
    invert() {
      const path = this.$route.fullPath;
      if (path === '/welcome' || path === '/') {
        return true;
      }
    },
    userName() {
      return this.$store.getters.userName;
    },
    isAdmin(){
      return this.$store.getters.isAdmin;
    },
    loggedIn(){
      return this.$store.getters.isLoggedIn;
    }
  },
  components: {
    logout,
    backButton,
    toggleSidepanel,
    blackout,
    sidepanel
  },
  watch: {
    '$route' (to, from) {
      this.showSidepanel = false;
    }
  },
  mixins: [switchToView]
}
</script>

<style lang="scss" scoped>
  @import '@/styles/variables.scss';
  
  header {
    //position: absolute;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    z-index: 40;
  }
  .header__inner {
    max-width: 600px;
    margin: 0 auto;
    position: relative;
  }
  
  .toggle-sidepanel {
    .invert &:not(.active) {
      color: #fff;
    }
  }

</style>